.news.lister {
    ul {
        padding: 0; }
    li {
        list-style: none; }
    .item {
        border-bottom: var(--border-line); }
    .context {
        justify-content: flex-start; }
    .thumb {
        width: 25%;
        margin-right: 2em; }
    .thumb img {
        aspect-ratio: 6 / 4.5; }
    .post-title {
        font-size: 1.25em;
        font-weight: 600; } }
