.news.canal {
    ul {
        padding: 0; }
    ul li {
        list-style: none; }
    .feat {
        border-bottom: var(--border-line);
        .thumb {
            grid-column: 4 / span 2; }
        .thumb img {
            aspect-ratio: 8 / 4.5; }
        .post-title {
            font-size: 1.8em;
            font-weight: 600; } }
    .list {
        grid-gap: 1em;
        .context {
            width: 100%; }
        .item {
            padding-right: 1em;
            border-right: var(--border-line); }
        .item:nth-last-child(1) {
            border-right: 0;
            padding-right: 0em; }
        .meta {
            width: calc( 100% - 93px ); }
        .thumb {
            width: 93px; }
        .thumb img {
            aspect-ratio: 1 / 1; } } }
