.news {
    a:not(.button) {
        color: var(--text); }
    .headline,
    .stories,
    .canal {
        ul {
            padding-inline-start: 0; }
        li {
            list-style: none; } }
    .thumb img {
        aspect-ratio: 8 / 5;
        object-fit: cover; }
    .author {
        font-size: .65em;
        text-transform: uppercase; }
    .terms {
        text-transform: uppercase;
        font-size: .665em;
        font-weight: 500; }
    .post-title {
        font-weight: 500;
        font-size: .975em;
        text-transform: capitalize; }
    .excerpt {
        margin-top: 65%;
        font-size: .85em; } }
