.headtag {
    width: 100%;
    color: var(--text);
    border-top: var(--border-line);
    justify-content: flex-start;
    .title {
      width: fit-content;
      font-size: .75em;
      font-weight: 600;
      text-transform: uppercase;
      background: var(--accent);
      border: var(--border-line);
      border-top: 0;
      padding: 5px 15px; }
    .tagline {
      font-size: .75em;
      text-transform: capitalize;
      line-height: 0;
      margin: 3px 0px 0px 1.5em;
      font-weight: 600; } }
