.news.single {
    width: 86%;
    margin: 0 auto;
    .post {
        grid-column: 2 / span 2; }
    .post-meta {
        h1 {
            text-transform: capitalize; }
        .published {
            border-right: var(--border-line);
            padding-right: 0.75em;
            margin-right: 0.75em; }
        .keyword {
            .terms {
                margin-right: .75em; } }
        .feature img {
            aspect-ratio: 6 / 3.5;
            object-fit: cover; } }
    .divider {
        margin: 2em 0;
        grid-gap: 0em; }
    .content {
        grid-column: 4 / span 2 ;
        text-align: justify;
        padding-right: 5em; }
    .canal-rel {
        ul {
            padding: 0;
            li {
                list-style: none; } }
        .context {
            width: 100%; }
        .meta {
            width: 100%; }
        .thumb {
            width : 92px; }
        .thumb img {
            aspect-ratio: 1/1; } } }

